/* eslint-disable */
import React from 'react';
import { Link } from 'react-router-dom';
import styles from './Header.module.css';
import styled from 'styled-components';
import { useEffect, useState } from 'react';
import { throttle } from 'lodash';
import ModeChanger from './ModeChanger';
import { useRecoilValue } from 'recoil';
import { cartsListAtom } from '../recoil/cartsList';

function Header( props ) {
    //   <Header />    Start
    const [scrollPosition, setScrollPosition] = useState(0); // scrollPosition  state .
    
    const updateScroll = () =>{
        setScrollPosition(window.scrollY || document.documentElement.scrollTop); // Y  .
    }

    const throttleScroll = throttle(updateScroll, 100); // updateScroll  1   . Lodash  .

    useEffect( () => {
        window.addEventListener('scroll', throttleScroll); // scroll  handlesScroll  .
        return () => {
            window.removeEventListener('scroll', throttleScroll); // clean up.
        }
    }, []);

    // console.log(scrollPosition);

    const[visible, setVisible] = useState(false);    
    useEffect( () => {                      //   Y useEffect   .
        if( scrollPosition < 100){
            setVisible(false);
        }
        else { setVisible(true); }
    })

    const checker = visible ? 'smallHeader' : 'hidden';
    //   <Header />    End


    const[isHover, setIsHover] = useState(false);

    const cartsList = useRecoilValue(cartsListAtom);
    // console.log(cartsList);
    // console.log(cartsList.length);

    return (
        <div className={styles.header}>
            <div className={styles.normalHeaderInner}>
                <div className={scrollPosition < 100 ? 'styles.normalHeader' : 'stlyes.hidden'}>
                    <div className={styles.top}>
                        <Link to="SignUp"></Link>
                        <Bar />
                        <Link to="SignIn"></Link>
                        <Bar />
                        <Link to="/"><span className={styles.underImg} /></Link>
                    </div>
                    <div className={styles.middle}>
                        <Link to="/">
                            <div className={styles.logoContainer}>
                                <img src='data:image/svg+xml;base64,' alt=" " />
                            </div>
                        </Link>
                        <ModeChanger />
                        <div className={styles.searchBar}>
                            <input type="text" className={styles.searchInput} placeholder=' .'/>
                            <button className={styles.searchButton}></button>
                        </div>
                        <div className={styles.userBox}>
                            <div className={styles.buttonContainer}>
                                <button className={styles.mapButton}></button>
                            </div>
                            <button className={styles.likeButton}></button>
                            <div className={styles.buttonContainer}>
                                <Link to='Cart'>
                                    <button className={styles.cartButton}>
                                        { cartsList.length >= 1 ? (
                                            <span className={styles.cartCount}> {cartsList.length} </span>
                                        ) : (
                                            ""
                                        )}
                                    </button>
                                </Link>
                            </div>
                        </div>
                    </div>
                    <div className={styles.bottom}>
                        <div className={styles.category}
                            onMouseOver = { () => {setIsHover(true);}}
                            onMouseOut = { () => {setIsHover(false);}}
                        >
                            <img className={styles.categoryImg} src={ isHover ? 'data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTYiIGhlaWdodD0iMTQiIHZpZXdCb3g9IjAgMCAxNiAxNCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KICAgIDxwYXRoIGQ9Ik0wIDBoMTZ2MS43SDBWMHptMCA2LjE1aDE2djEuN0gwdi0xLjd6bTAgNi4xNWgxNlYxNEgwdi0xLjd6IiBmaWxsPSIjNUYwMDgwIiBmaWxsLXJ1bGU9ImV2ZW5vZGQiLz4KPC9zdmc+Cg==' : 'data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTYiIGhlaWdodD0iMTQiIHZpZXdCb3g9IjAgMCAxNiAxNCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KICAgIDxwYXRoIGQ9Ik0wIDBoMTZ2MS43SDBWMHptMCA2LjE1aDE2djEuN0gwdi0xLjd6bTAgNi4xNWgxNlYxNEgwdi0xLjd6IiBmaWxsPSIjMzMzIiBmaWxsLXJ1bGU9ImV2ZW5vZGQiLz4KPC9zdmc+Cg=='} alt=' ' />
                            <span className={styles.categoryText}></span>
                        </div>
                        <ul className={styles.navMenu}>
                            <li className={styles.menuList}><span></span></li>
                            <li className={styles.menuList}><span></span></li>
                            <li className={styles.menuList}><span></span></li>
                            <li className={styles.menuList}><span>/</span></li>
                        </ul>
                        <div className={styles.delivery}>
                            <a href='' className={styles.deliveryTag}>
                                <span></span>
                            </a>
                        </div>
                    </div>
                </div>
                {/* <div className={`${styles[scrollPosition > 100 ? 'smallHeader' : 'hidden']}`}> */}
                <div className={`${styles[checker]}`}>
                    <div className={styles.smallHeaderInner}>
                        <div className={styles.category}
                            onMouseOver = { () => {setIsHover(true);}}
                            onMouseOut = { () => {setIsHover(false);}}
                        >
                            <img className={styles.categoryImg} src={ isHover ? 'data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTYiIGhlaWdodD0iMTQiIHZpZXdCb3g9IjAgMCAxNiAxNCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KICAgIDxwYXRoIGQ9Ik0wIDBoMTZ2MS43SDBWMHptMCA2LjE1aDE2djEuN0gwdi0xLjd6bTAgNi4xNWgxNlYxNEgwdi0xLjd6IiBmaWxsPSIjNUYwMDgwIiBmaWxsLXJ1bGU9ImV2ZW5vZGQiLz4KPC9zdmc+Cg==' : 'data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTYiIGhlaWdodD0iMTQiIHZpZXdCb3g9IjAgMCAxNiAxNCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KICAgIDxwYXRoIGQ9Ik0wIDBoMTZ2MS43SDBWMHptMCA2LjE1aDE2djEuN0gwdi0xLjd6bTAgNi4xNWgxNlYxNEgwdi0xLjd6IiBmaWxsPSIjMzMzIiBmaWxsLXJ1bGU9ImV2ZW5vZGQiLz4KPC9zdmc+Cg=='} alt=' ' />
                            <span className={styles.categoryText}></span>
                        </div>
                        <ul className={styles.navMenu}>
                            <li className={styles.menuList}><span></span></li>
                            <li className={styles.menuList}><span></span></li>
                            <li className={styles.menuList}><span></span></li>
                            <li className={styles.menuList}><span>/</span></li>
                        </ul>
                        <div className={styles.smallSearchBar}>
                            <input type="text" className={styles.searchInput} placeholder=' .'/>
                            <button className={styles.smallSearchButton}></button>
                        </div>
                        <div className={styles.userBox}>
                            <div className={styles.buttonContainer}>
                                <button className={styles.mapButton}></button>
                            </div>
                            <button className={styles.likeButton}></button>
                            <div className={styles.buttonContainer}>  
                                <Link to='Cart'>                            
                                    <button className={styles.cartButton}>
                                        { cartsList.length >= 1 ? (
                                            <span className={styles.cartCount}> {cartsList.length} </span>
                                        ) : (
                                                ""
                                        )}
                                    </button>
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

const Bar = styled.div`
    width: 1px;
    height: 13px;
    margin: 0px 12px;
    background-color: rgb(217, 217, 217);
`

export default Header;